










































































import {Component, Inject, Mixins} from "vue-property-decorator";
import TableStateController from "@/mixins/tableStateController";
import Realty from "@/models/Realty";
import EquipmentModel from "@/models/Equipment";
import {AxiosResponse} from "axios";
import {responseWithPaginator} from "@/common/types";
import {getModule} from "vuex-module-decorators";
import Notification from "@/store/modules/notification";
import ItemsCountInfo from "@/components/ItemsCountInfo.vue";
import ModalDeletingConfirm from "@/components/ModalDeletingConfirm.vue";

@Component({
  components: {ModalDeletingConfirm, ItemsCountInfo}
})
export default class Equipment extends Mixins<TableStateController>(TableStateController) {
  showConfirmModal = false
  items = [] as Array<Realty>
  equip = null as null | EquipmentModel
  @Inject('basePath') basePath!: string
  fields = [
    {
      key: 'selected',
      label: ''
    },
    {
      key: 'img_path',
      label: ''
    },
    {
      key: 'name',
      label: 'Описание',
      searchable: true,
      sortable: true,
    },
    {
      key: 'realtyTypeName',
      label: 'Тип',
      searchable: true
    },
    {
      key: 'area',
      label: 'Площадь',
      sortable: true,
      searchable: true
    },
    {
      key: 'price',
      label: 'Стоимость (руб.)',
      sortable: true,
      searchable: true
    },
    {
      key: 'discount_sum',
      label: 'Скидка (руб.)',
      sortable: true,
      searchable: true
    },
    {
      key: 'price_per_metr',
      label: 'Стоимость за м. кв.',
      sortable: true,
      searchable: true
    },
    {
      key: 'updated_at',
      label: 'Изменен',
      sortable: true,
    },
    {
      key: 'created_at',
      label: 'Создан',
      sortable: true,
    },
  ]

  get selectionBtnText(): string {
    return this.selectedAllRows ? 'Снять выделение' : 'Выбрать все'
  }

  updateTableData(): Promise<AxiosResponse<responseWithPaginator<Realty>>> {
    return Realty.getList({...this.tableOptionsCleared, equipments: [this.$route.params.id]})
        .then(response => {
          this.items = response.data.data

          return response
        })
  }

  onConfirm(): void {
    Realty.destroy(this.selected.map(value => value.id as number)).then(() => {
      getModule(Notification, this.$store).setData({title: 'Удаление прошло успешно', variant: 'success'})
      this.updateTableData();
    })

  }

  onDelete(): void {
    this.showConfirmModal= true
  }

  created(): void {
    EquipmentModel.get({id: this.$route.params.id as unknown as number}).then(res => {
      this.equip = res.data
    })
  }

}
